<style lang="less" scoped>
.deviceRenameWrap {
  background-color: #fff;
  padding-left: 20rpx;
  font-size: 12px;
  height: 60rpx;
}
.saveName {
  margin-top: 80rpx;
  transform: scale(0.85);
  border-radius: 25px;
  background-color: #DC851F;
  color: #fff;
}
</style>
<template>
  <view class="renameWrap">
    <input class="deviceRenameWrap" type="text" v-model="deviceName" focus confirm-type="done"/>
    <button class="saveName" @click="saveDeviceName(deviceName)">保存</button>
  </view>
</template>

<script>
  export default {
    data() {
      return {
        deviceName: ""
      }
    },
    onLoad (val) {
      this.deviceName = val.deviceName
    },
    methods: {
      // 保存设备的名称
      saveDeviceName (deviceName) {
        uni.$emit("getRenameDevice", this.deviceName)
        uni.navigateBack({
          delta: 1
        })
        // 发送请求修改数据
        
      }
    }
  }
</script>


